.Header {
  box-sizing: border-box;
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  z-index: 1100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 40px;
  padding-right: 24px;
  padding-left: 50px;
  padding-top: 12px;
  padding-bottom: 12px;
  box-shadow: 0px 0.5px 0.5px -0.5px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 0.5px 0.5px 0px rgba(0, 0, 0, 0.12);
  width: 100vw;

  color: #ffffff5b;

  text-align: center;
  backdrop-filter: blur(20px);
}
